import React from 'react';
import PropTypes from 'prop-types';
import { Panel, Button, Group, Div, PanelHeader, FormLayout, FormLayoutGroup, Input, InfoRow } from '@vkontakte/vkui';
import costs from './costs'
import './Home.css';

class Home extends React.Component {  
    
    constructor(props) {  
        super(props);  

        this.state = {  
        	id: "home",
            result: "",
            currentBus: "",
            isTrolleybus: false
        };  
        this.updateMessage = this.updateMessage.bind(this);  
        this.onChangeBus = this.onChangeBus.bind(this); 
        this.rubtostr = this.rubtostr.bind(this); 
    }
   	updateMessage = (e) => {
   		this.setState(state => ({
	      result: ""
	    }));
   		var rez = 0;
   		var objbus = costs[this.state.currentBus];
   		if (objbus) {
   			console.log(this.state.isTrolleybus);
   			//if (!objbus.isTrolleybus)
   			rez = objbus.cost;
   		}

   		if (!rez) 
   			rez = "Нет данных";
   		else
   			rez = rez + " " + this.rubtostr(rez);
   		
		this.setState(state => ({
	      result: rez
	    }));
	}
	onChangeBus = (e) => {
		this.setState({ currentBus: e.target.value });
	}		
	onChangeTrolleybus = (e) => {
		this.setState({ isTrolleybus: e.target.value });
	}
	rubtostr = (n) => {
		var text_forms = ['рубль', 'рубля', 'рублей'];
	  	n = Math.abs(n) % 100;
	  	var n1 = n % 10;
	  	if (n > 10 && n < 20) return (text_forms[2]);
	  	if (n1 > 1 && n1 < 5) return (text_forms[1]);
	  	if (n1 == 1) return (text_forms[0]);
	  	return (text_forms[2]);
	}
    render() {  
         return (  
           <Panel id={this.state.id} theme="white">
           		<PanelHeader>
            		Цена проезда в Пензе
          		</PanelHeader>
          	<FormLayout>
		      <FormLayoutGroup top="Номер маршрутки/автобуса/троллейбуса">
		      	<Input type="text" alignment="center" placeHolder="Введите номер"  onChange={this.onChangeBus} />
		       </FormLayoutGroup>
		       <FormLayoutGroup>
		       <center>
		        <input type="radio" value="1" name="typeBus" checked={1} onChange={this.onChangeTrolleybus}/>Автобус/маршрутка   
		        <input type="radio" value="2" name="typeBus"  onChange={this.onChangeTrolleybus}/>Троллейбус
		       </center>
		      </FormLayoutGroup>
	          <Button size="xl" level="primary" stretched="true" onClick={this.updateMessage}>Сколько стоит проезд?</Button>
		      <Div alignment="center">
			      <InfoRow className="resultcost">
			      	<center>{this.state.result}</center>
			      </InfoRow>
		      </Div>
		      </FormLayout>
           </Panel> 
        )  
    }  
}

 export default Home;